function cyc_stegger() {
    this.option = {
        lengthSize: 24,
        charSize: 16,
        mixCount: 2
    }
    this.binaryToStr = function (str) {
        let result = [];
        let list = str.split(" ");
        for (let i = 0; i < list.length; i++) {
            let item = list[i];
            //console.log(item)
            let asciiCode = parseInt(item, 2);
            //console.log(asciiCode)
            let charValue = String.fromCharCode(asciiCode);
            // console.log(charValue)
            result.push(charValue);
        }
        return result.join("");
    }
    this.encrypt = function (src, canvas, mm) {
        return new Promise((resolve) => {
            let img = new Image;
            img.src = src;
            mm = mm + "";
            img.onload = function () {
                canvas.width = img.width;
                canvas.height = img.height;
                let ctx = canvas.getContext('2d');

                let pixelCount = ctx.canvas.width * ctx.canvas.height;
                // if ((24 + mm.length * 16) > (pixelCount * 3 * 2)) {
                //     throw '文本内容对于图像来说太长了';
                // }
                // 将图片画到canvas上面上去
                ctx.drawImage(img, 0, 0);
                let imgData = ctx.getImageData(0, 0, img.width, img.height);
                // console.log(imgData.data.length)
                let list = [];
                // 
                mm = (mm.length + ";") + mm;
                for (let i = 0; i < mm.length; i++) {
                    list.push(mm.charCodeAt(i).toString(2).padStart(16, 0))
                }
                let wrlist = list.join('');
                //写入
                let weindex = 0;
                for (let i = 0; i < wrlist.length; i++) {
                    if (!imgData.data[weindex + 3].toString(2).endsWith(wrlist[i])) {//11111111 != 0
                        imgData.data[weindex + 3] = parseInt(imgData.data[weindex + 3].toString(2).substring(0, 7) + (wrlist[i].endsWith('0') ? '0' : '1'), 2)//2进制转10进
                    }
                    weindex += 4
                }
                // console.log(imgData.data.length)
                // console.log(imgData.data.join('>>'))
                // console.log(imgData)
                //生成新的图片
                ctx.putImageData(imgData, 0, 0);
                resolve(canvas.toDataURL())
            }
        })
    }
    this.solution = function (src2, canvas) {
        return new Promise((resolve) => {
            let img2 = new Image;
            img2.src = src2;//'./red2.jpg';
            img2.onload = () => {
                canvas.width = img2.width;
                canvas.height = img2.height;

                let ctx = canvas.getContext('2d');
                // 将图片画到canvas上面上去！
                ctx.drawImage(img2, 0, 0);
                let imgData = ctx.getImageData(0, 0, img2.width, img2.height);
                console.log(imgData.data.join('>>'))
                // //解析数据
                let list = []
                for (let i = 3; i < imgData.data.length; i += 4) {
                    list.push((imgData.data[i].toString(2) + '').substring(7, 8))
                }
                list = list.join('')
                let strs = "";
                let dqlength = 0;
                let bjlength = 0;
                for (let i = 0; i < (dqlength || list.length); i += 16) {
                    // console.log(strs)
                    strs += this.binaryToStr(list.substring(i, i + 16))
                    if (strs && strs.indexOf(';') != -1 && dqlength == 0) {//读取到有长度符
                        dqlength = strs.substring(0, strs.indexOf(';'))
                        bjlength = dqlength.length + 1
                        dqlength = (bjlength + parseInt(dqlength)) * 16;
                    }
                }
                strs = strs.substr(bjlength)
                // console.log('图片内容:' + strs)
                resolve(strs)

            }
        })

    }
}

export default new cyc_stegger()