<template>
<div class="senbd">
    <div v-if="encrypt">
        <div class="top flex">
            <div class="tipinfo">{{onlinePressbinary}}</div>
            <div class="press">
                <div :style="{width:onlinePress+'%'}"></div>
            </div>
        </div>
        <div class="content">
            <div class="fls">
                <img v-if="carrierUrl" :src="carrierUrl"/>
            </div>
                <div class="channel">
                    <p>加入</p>
                </div>
                <div class="cipher">
                    <div v-if="carrierUrlOk">
                        <img :src="carrierUrlOk"/>
                    </div>
                        <textarea v-if="!carrierUrlOk" v-model="carrierText" rows="4" style="resize:none" ></textarea>
                    </div>
                </div>
                <div class="bottom">
                    <div @click="carrierFile" class="cy-btn">秘载体</div>
                    <div @click="loadData2File" class="cy-btn">装载数据</div>
                    <div v-if="carrierUrlOk" @click="downloadCarrier" class="cy-btn">下载</div>
                </div>
            </div>
            <div v-if="solution">
                <div class="top flex">
                    <div class="tipinfo">{{onlinePress2binary}}</div>
                    <div class="press">
                        <div :style="{width:onlinePress2+'%'}"></div>
                    </div>
                </div>
                <div class="content">
                    <div class="fls">
                        <img v-if="solutionFileUrl" :src="solutionFileUrl"/>
            </div>
                        <div class="channel">
                            <p>取出</p>
                        </div>
                        <div class="cipher">
                            <textarea v-model="solutionText" rows="4" style="resize:none" ></textarea>
                        </div>
                    </div>
                    <div class="bottom">
                        <div @click="solutionFile" class="cy-btn">选择秘载体</div>
                        <div @click="solutionFile2Encrypt" class="cy-btn">数据分离</div>
                    </div>
                </div>
                <input accept="image/*" type="file" :id="sendInputFileId" @change="outputFile" style="display:none"/>
                <canvas :id="encryptCanvasID" style="display:none"></canvas>
                <canvas :id="solutionCanvasID" style="display:none"></canvas>
            </div>
</template>

<script>
import {
    mapGetters
} from 'vuex'

import func from '../../utils/func'
import cxy_stegger from '../../utils/cxy_stegger'

export default {
    name: 'PriceSendData',
    data: function () {
        return {
            fileIndex: '',
            onlinePress: '', //效果
            onlinePressbinary: '', //效果
            onlinePress2: '', //效果
            onlinePress2binary: '', //效果
            sendInputFileId: '', //inputfle
            settimekey: '', //效果time
            encryptCanvasID: '', //载体canvas输出
            solutionCanvasID: '', //
            carrierText: '', //载体的密文
            solutionText: '', //解密载体的密文
            carrierUrl: '', //载体url
            carrierUrlOk: '', //载体完成url
            solutionFileUrl: '', //解密载体url
            solutionFileUrlOk: '' //解密载体完成url
        }
    },
    props: {
        encrypt: Boolean,
        solution: Boolean
    },
    computed: {
        ...mapGetters([
            'getNavItm'
        ])
    },
    methods: {
        //在线效果,想法:随机100以内的数字,然后转换成2进制,数字做进度
        online: function () {
            console.log(1)
            let rmky = func.randomId()
            this.onlinePress = func.intervalRandom(0, 100)
            this.onlinePressbinary = rmky.charCodeAt(0).toString(2).padStart(8, 0) + '' + rmky.charCodeAt(1).toString(2).padStart(8, 0)
            this.onlinePress2 = func.intervalRandom(0, 100)
            this.onlinePress2binary = rmky.charCodeAt(2).toString(2).padStart(8, 0) + '' + rmky.charCodeAt(3).toString(2).padStart(8, 0)
        },
        //文件选择后的输出
        outputFile: function (e) {
            if (e.target.files.length != 0) {
                // console.log(e)
                console.log(e.target.files[0].size)
                // if (e.target.files[0].size > 1000000) {
                //     alert('图片太大,目前只支持10MB一下文件')
                //     return
                // }
                if (e.target.accept.indexOf('image') != -1) {
                    if (this.fileIndex == 0) {
                        let url = window.URL.createObjectURL(e.target.files[0])
                        this.solutionFileUrl = url
                        console.log(url)
                    } else if (this.fileIndex == 1) {
                        let url2 = window.URL.createObjectURL(e.target.files[0])
                        this.carrierUrl = url2
                        console.log(url2)
                    }
                }
            }
        },
        //载体选择
        carrierFile: function () {
            this.fileIndex = 1
            this.$nextTick(function () {
                document.getElementById(this.sendInputFileId).click()
            })
        },
        //解密载体
        solutionFile: function () {
            this.fileIndex = 0
            this.$nextTick(function () {
                document.getElementById(this.sendInputFileId).click()
            })
        },
        //加载数据到file中
        loadData2File: function () {
            if (!this.carrierUrl) {
                return;
            }
            if (!this.carrierText) {
                return;
            }
            cxy_stegger.encrypt(
                this.carrierUrl,
                document.getElementById(this.encryptCanvasID),
                this.carrierText
            ).then((url) => {
                this.carrierUrlOk = url
            })
        },
        //解密
        solutionFile2Encrypt: function () {
            if (this.solutionFileUrl) {
                cxy_stegger.solution(
                    this.solutionFileUrl,
                    document.getElementById(this.solutionCanvasID)
                ).then((text) => {
                    this.solutionText = text
                })
            }
        },
        //下载图片
        downloadCarrier: function () {
            // 如果浏览器支持msSaveOrOpenBlob方法（也就是使用IE浏览器的时候），那么调用该方法去下载图片
            const imgUrl = this.carrierUrlOk
            if (window.navigator.msSaveOrOpenBlob) {
                var bstr = atob(imgUrl.split(',')[1])
                var n = bstr.length
                var u8arr = new Uint8Array(n)
                while (n--) {
                    u8arr[n] = bstr.charCodeAt(n)
                }
                var blob = new Blob([u8arr])
                window.navigator.msSaveOrOpenBlob(blob, 'chart-download' + '.' + 'png')
            } else {
                // 这里就按照chrome等新版浏览器来处理
                const a = document.createElement('a')
                a.href = imgUrl
                a.setAttribute('download', 'chart-download')
                a.click()
            }
        }
    },
    mounted: function () {
        this.sendInputFileId = func.randomId()
        this.encryptCanvasID = func.randomId()
        this.solutionCanvasID = func.randomId()

        this.settimekey = window.setInterval(() => {
            this.online()
        }, 100)

        console.log(cxy_stegger)
    },
    beforeDestroy: function () {
        window.clearInterval(this.settimekey)
        this.settimekey = null
    }
}
</script>

<style scoped>
.senbd {
    border: 2px dashed #283452;
    height: 300px;
    border-top: 2px solid #00546f;
    border-bottom: 2px solid #00546f;
    border-radius: 14px;
    padding: 6px 16px;
    color: #999;
    font-weight: bold;
}

.senbd .content {
    display: flex;
    height: 251px;
}

.senbd .content .fls {
    flex: 1;
}

.senbd .content .fls img {
    margin-top: 10%;
    margin-left: 5%;
    width: 240px;
    height: 200px;
}

.senbd .content .channel {
    width: 60px;
    text-align: center;
    display: flex;
    align-items: center;
    font-size: 14px;
    font-weight: bold;
}

.senbd .content .cipher {
    flex: 1;
}

.senbd .content .cipher img {
    margin-top: 10%;
    margin-left: 5%;
    width: 240px;
    height: 200px;
}

.senbd .tipinfo {
    width: 122px;
    font-size: 12px;
}

.senbd .press {
    width: 160px;
    height: 12px;
    border: 1px solid #283452;
}

.senbd .press div {
    height: 10px;
    margin-top: 1px;
    background: #999;
}

.cy-btn {
    height: 24px;
    line-height: 24px;
    background: #999999;
    border-radius: 0px;
    border: 0px solid #00546f;
    margin-right: 16px;
}

.senbd .content textarea {
    background: #0c1326;
    resize: none;
    width: 90%;
    height: 95%;
    color: #999;
    font-family: auto;
    padding: 10px;
    border-color: #00546f;
}

.senbd .content textarea:focus {
    outline: none;
}
</style>
